import { IViewActionDesignManager } from '@helix/platform/view/api';
import { IViewComponentDesignValidationIssue } from '@helix/platform/view/designer';
import { Observable, of } from 'rxjs';
import { Injectable } from '@angular/core';
import { ISetPropertyViewActionDesignProperties } from './set-property-view-action.interfaces';

@Injectable()
export class RxSetPropertyViewActionDesignManagerService
  implements IViewActionDesignManager<ISetPropertyViewActionDesignProperties> {
  validate(
    actionProperties: ISetPropertyViewActionDesignProperties,
    propertyName: string
  ): Observable<IViewComponentDesignValidationIssue[]> {
    return of(
      actionProperties.propertyPath && !/\${view\.components\.([0-9a-z-]+)\..+}/.test(actionProperties.propertyPath)
        ? [
            {
              type: 'error',
              description: 'Set property action: Property path is invalid.',
              propertyName
            }
          ]
        : []
    );
  }
}
